import Login from './views/Login.vue'
import Reg from './views/reg.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import Page4 from './views/nav2/Page4.vue'
import Page5 from './views/nav2/Page5.vue'
import Page6 from './views/nav3/Page6.vue'
import echarts from './views/charts/echarts.vue'
//管理员页面
import companyMemberManage from './views/administrator/companyMemberManage.vue'
import personMemberManage from './views/administrator/personMemberManage.vue'
import inviteInformatioionManage from './views/administrator/inviteInformatioionManage.vue'
import applyJobManage from './views/administrator/applyJobManage.vue'
//资料管理
import loginPassword from './views/dataManage/loginPassword.vue'
import personalInformation from './views/dataManage/personalInformation.vue'


let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/reg',
        component: Reg,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    // {
    //     path: '/',
    //     component: Home,
    //     name: '导航一',
    //     iconCls: 'el-icon-message',//图标样式class
    //     children: [
    //        // { path: '/main', component: Main, name: '主页', hidden: true },
    //         { path: '/table', component: Table, name: 'Table' },
    //         { path: '/form', component: Form, name: 'Form' },
    //         { path: '/user', component: user, name: '列表' },
    //     ]
    // },    
    //管理员页面
    {
        path: '/',
        component: Home,
        name: '管理员',
        iconCls: 'fa fa-address-card',//图标样式class
        children: [
            { path: '/companyMemberManage', component: companyMemberManage, name: '企业会员管理' },
            { path: '/personMemberManage', component: personMemberManage, name: '个人会员管理' },
            { path: '/inviteInformatioionManage', component: inviteInformatioionManage, name: '招聘信息管理' },
            { path: '/applyJobManage', component: applyJobManage, name: '求职信息管理' },
        ]
    },
    //资料管理
    {
        path: '/',
        component: Home,
        name: '资料管理',
        iconCls: 'fa fa-database',
        children: [
            { path: '/loginPassword', component: loginPassword, name: '登录密码' },
            { path: '/personalInformation', component: personalInformation, name: '个人信息' }
        ]
    },  

    // {
    //     path: '/',
    //     component: Home,
    //     name: '导航二',
    //     iconCls: 'fa fa-id-card-o',
    //     children: [
    //         { path: '/page4', component: Page4, name: '页面4' },
    //         { path: '/page5', component: Page5, name: '页面5' }
    //     ]
    // },
    // {
    //     path: '/',
    //     component: Home,
    //     name: '',
    //     iconCls: 'fa fa-address-card',
    //     leaf: true,//只有一个节点
    //     children: [
    //         { path: '/page6', component: Page6, name: '导航三' }
    //     ]
    // },
    // {
    //     path: '/',
    //     component: Home,
    //     name: 'Charts',
    //     iconCls: 'fa fa-bar-chart',
    //     children: [
    //         { path: '/echarts', component: echarts, name: 'echarts' }
    //     ]
    // },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


